.contact-header {
    font-family: Orbitron;
    font-size: 50px;
    text-align: center;
    margin: 100px 0px;
    color: var(--color3)
}

.footer-header {
    font-size: 2.5em;
    text-align: center;
}

.card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 15%;
}

@media screen and (max-width: 800px){
    .contact-header {
        font-size: 2em;
        flex-shrink: 2;
    }
}